.info {
  padding-top: 65px;
  padding-bottom: 60px;
  transition: .3s;
  &-title {
    font-size: 44px;
    line-height: 52px;
    text-align: center;
    color: #404040;
    margin-bottom: 36px;
    transition: .3s;
  }
  &-layout {
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
  }
  &-content {
    width: 100%;
    transition: .3s;
    &_l1 {
      padding-bottom: 40px;
    }
  }
  .sidebar {
    max-width: 340px;
    width: 100%;
    margin-right: 45px;
    transition: .3s;
    &_l1 {
      margin-right: 20px;
    }
    &_l2 {
      max-width: 438px;
      margin-right: 42px;
    }
    .nav-item {
      display: block;
      width: 100%;
      text-align: center;
      &:not(:last-child){
        margin-bottom: 8px;
      }
    }
    .nav-link {
      font-size: 24px;
      line-height: 28px;
      text-align: center;
      color: #526CB3;
      background: #DCE7FF;
      border-radius: 3px;
      padding: 14px 10px;
      transition: .3s;
      &.active,
      &:hover {
        background: #526CB3;
        color: #FFFFFF;
      }
    }
  }
  &-right-sidebar {
    max-width: 200px;
    width: 100%;
    margin-left: 20px;
    transition: .3s;
  }
}
.panel-default {
  border: 1px solid;
  border-radius: 5px;
  margin-bottom: 20px;
}
.panel-title {
  text-align: center;
  a {
    display: block;
    border-bottom: 1px solid;
  }
}
.panel-fix {
  max-height: 500px;
  overflow-x: auto;
}